"use client";

import * as React from "react";
import { format } from "date-fns";
import * as z from "zod";
import { zodResolver } from "@hookform/resolvers/zod";
import { useForm } from "react-hook-form";

import type { Tournament, TournamentEntry, User } from "@/lib/types";
import { events, partneredEvents } from "@/lib/events";
import { useAuth } from "@/contexts/auth-context";
import { useToast } from "@/hooks/use-toast";

import { cn } from "@/lib/utils";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { MultiSelect } from "@/components/ui/multi-select";
import { ExternalLink, UserPlus } from "lucide-react";

interface TournamentSignupCardProps {
  tournament: Tournament;
  users: User[];
  onTournamentUpdate: (tournament: Tournament) => void;
}

const signupSchema = z.object({
  events: z.array(z.string()).min(1, "Please select at least one event.").max(3, "You can select up to 3 events."),
  partnerId: z.string().optional(),
});

export default function TournamentSignupCard({ tournament, users, onTournamentUpdate }: TournamentSignupCardProps) {
  const { user } = useAuth();
  const { toast } = useToast();
  const [open, setOpen] = React.useState(false);

  const form = useForm<z.infer<typeof signupSchema>>({
    resolver: zodResolver(signupSchema),
    defaultValues: { events: [] },
  });

  const selectedEvents = form.watch("events");
  const needsPartner = selectedEvents.some(event => partneredEvents.includes(event));
  
  const userEntry = user ? tournament.entries.find(entry => entry.id === user.id) : undefined;

  function onSubmit(values: z.infer<typeof signupSchema>) {
    if (!user) return;
    
    if (needsPartner && !values.partnerId) {
        form.setError("partnerId", { message: "A partner is required for one of the selected events." });
        return;
    }

    const partner = users.find(u => u.id === values.partnerId);

    const newEntry: TournamentEntry = {
      id: user.id,
      name: user.name,
      events: values.events,
      partnerId: values.partnerId,
      partnerName: partner?.name,
    };

    const updatedEntries = tournament.entries.filter(entry => entry.id !== user.id).concat(newEntry);
    onTournamentUpdate({ ...tournament, entries: updatedEntries });
    
    toast({ title: "Successfully Signed Up!", description: `You are now registered for ${tournament.name}.` });
    setOpen(false);
    form.reset();
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle className="font-headline">{tournament.name}</CardTitle>
        <CardDescription>
          {format(new Date(tournament.date), "EEEE, MMMM d, yyyy")}
        </CardDescription>
      </CardHeader>
      <CardContent className="flex flex-col gap-2">
         {tournament.webpageUrl && (
              <Button variant="outline" size="sm" asChild>
                <a href={tournament.webpageUrl} target="_blank" rel="noopener noreferrer">
                  <ExternalLink className="mr-2 h-3 w-3"/> Tournament Webpage
                </a>
              </Button>
            )}
            {tournament.scheduleUrl && (
              <Button variant="outline" size="sm" asChild>
                <a href={tournament.scheduleUrl} target="_blank" rel="noopener noreferrer">
                 <ExternalLink className="mr-2 h-3 w-3"/> Tournament Schedule
                </a>
              </Button>
            )}
      </CardContent>
      <CardFooter>
        <Dialog open={open} onOpenChange={setOpen}>
          <DialogTrigger asChild>
            <Button className="w-full">
              {userEntry ? 'Update Entry' : 'Sign Up'}
            </Button>
          </DialogTrigger>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>Sign Up for {tournament.name}</DialogTitle>
              <DialogDescription>
                Select up to 3 events. Partnered events require a partner.
              </DialogDescription>
            </DialogHeader>
            <Form {...form}>
              <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6 py-4">
                <FormField
                  control={form.control}
                  name="events"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Events</FormLabel>
                      <MultiSelect
                        options={events.map(e => ({ label: e, value: e }))}
                        onValueChange={field.onChange}
                        defaultValue={field.value}
                        placeholder="Select up to 3 events..."
                        maxCount={3}
                      />
                      <FormMessage />
                    </FormItem>
                  )}
                />
                {needsPartner && (
                  <FormField
                    control={form.control}
                    name="partnerId"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>Partner</FormLabel>
                        <Select onValueChange={field.onChange} defaultValue={field.value}>
                          <FormControl>
                            <SelectTrigger>
                              <SelectValue placeholder="Select a partner" />
                            </SelectTrigger>
                          </FormControl>
                          <SelectContent>
                            {users.filter(u => u.id !== user?.id).map(u => (
                              <SelectItem key={u.id} value={u.id}>
                                {u.name}
                              </SelectItem>
                            ))}
                          </SelectContent>
                        </Select>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                )}
                <DialogFooter>
                  <Button type="submit">Submit Entry</Button>
                </DialogFooter>
              </form>
            </Form>
          </DialogContent>
        </Dialog>
      </CardFooter>
    </Card>
  );
}
